.auth__title {
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-weight: 900;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;
    margin: 0 0 27px;
}

@media screen and (min-width:768px) {
    .auth__title {
        font-size: 24px;
        line-height: 29px;
        margin: 0 0 37px;
    }
}