.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 300px;
}

@media screen and (min-width:768px) {
  .profile {
    flex-direction: row;
    justify-content: space-between;
    padding: 40px 0 15px;
    max-width: none;
  }
}
