 .header__link {
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-align: right;
    color: #FFFFFF;
    text-decoration: none;
    transition: opacity .2s ease-in-out;
    opacity: 1;
}

.header__link:hover {
    opacity: .6;
}

@media screen and (min-width:768px) {
    .header__link {
        font-size: 18px;
        line-height: 1.22;
    }
}